import React, { forwardRef } from "react"
import { SxProps, Typography } from "@rp/react/web/mui/core"
import { BaseChipStyled } from "./Chip.styles"

type BaseChipProps = {
  onClick?: (event: React.MouseEvent<HTMLElement>) => void
  onRemove?: () => void
  icon?: React.ReactElement
  operation?: string
  children: React.ReactNode
  clickable?: boolean
  className?: string
  selected?: boolean
  sx?: SxProps
}

const operationMap: Record<string, string> = {
  all: "AND",
  "not-in": "NOT",
}

const BaseChip = forwardRef<HTMLDivElement, BaseChipProps>(
  (
    {
      operation,
      icon,
      onRemove,
      onClick = f => f,
      clickable = false,
      children,
      className = "",
      selected = false,
      sx,
    },
    ref,
  ) => {
    const Icon =
      operation !== undefined && operation !== "in" ? (
        <>
          <Typography
            style={{ marginLeft: 10, fontSize: 12, letterSpacing: 0.75 }}
            data-testid={`operator-${operationMap[operation]}`}
          >
            {operationMap[operation]}
          </Typography>
          {icon}
        </>
      ) : (
        icon
      )
    return (
      <BaseChipStyled
        className={`${className}${selected ? " selected" : ""}`}
        label={children}
        variant="outlined"
        icon={Icon}
        onClick={onClick}
        onDelete={onRemove}
        size="small"
        clickable={clickable}
        selected={selected}
        sx={sx}
        ref={ref}
      />
    )
  },
)

export { BaseChip }
export type { BaseChipProps }
